<template>
  <div class="todo">
    <section class="add-box">
      <input autofocus class="add-todo" type="text" placeholder="准备做什么？" @keyup.enter="addTodo" v-model="newTodo" >
    </section>

    <section class="main">
      <div class="active-todos" >
        <div v-if="activeTodos.length != 0">
          <h3>待办事项</h3>
          <ul class="">
            <li v-for="todo in activeTodos" :key="todo.id"  @click="toggleTodo(todo)">
              <input type="checkbox" v-model="todo.done" name="">{{todo.content}}
            </li>
          </ul>
        </div>
        <p class="list-empty" v-if="activeTodos.length === 0">✔️ 恭喜你，所有的任务都完成了</p>
      </div>
      
      <div class="completed-todos" v-if="doneTodos != 0">
        <h3>已完成</h3>
        <ul v-show="isShowDone">
          <li v-for="todo in doneTodos" :key="todo.id" @click="toggleTodo(todo)">
            <input type="checkbox" v-model="todo.done" checked name="" id="">{{todo.content}}
            <span class="remove-todo" title="删除该条记录" @click="removeDone(todo)">✖️</span>
          </li>
        </ul>
        <p style="text-align: center;cursor:pointer;font-size:.9em;" v-show="!isShowDone" @click="showDoneList">点击查看已完成记事</p>
      </div>
    </section>

    <section class="footer">
      <div class="action">
        <span @click="removeAllDone">✖️ 删除所有已完成记事</span>
        <span @click="removeAllTodo">✖️ 删除所有记事</span>
        <span v-if="isShowDone" @click="showDoneList">⚪️ 折叠已完成记事</span>
        <span v-else @click="showDoneList">⚪️ 显示已完成记事</span>
      </div>
      <div  style="text-align: center;font-size:.9em;">
        输入框回车即可完成添加
      </div>
    </section>
  </div>
</template>

<script>
import { storage_todo, timestamp, filters } from '@/api/todo'

export default {
  data() {
    return {
      todos: storage_todo.fetch(),
      newTodo: '',
      isShowDone: true
    }
  },
  watch: {
    todos: {
      handler: function(todos) {
        storage_todo.save(todos)
      },
      deep: true
    }
  },
  computed: {
    activeTodos: function() {
      return filters.active(this.todos) || []
    },
    doneTodos: function() {
      return filters.done(this.todos) || []
    }
  },
  methods: {
    getTodos: function() {},
    addTodo: function() {
      let value = this.newTodo && this.newTodo.trim()
      if (!value) {
        console.log('empty')
        return
      }
      this.todos.push({
        id: timestamp(),
        content: value,
        done: false
      })
      this.newTodo = ''
    },
    toggleTodo: function(todo) {
      todo.done = !todo.done
      // console.log(this.todos)
    },
    removeDone: function(todo) {
      if (confirm('确定要删除这件记事么？')) {
        this.todos.splice(this.todos.indexOf(todo), 1)
      }
    },
    removeAllDone: function(todo) {
      this.todos = this.activeTodos;   /* 删除已完成的，令有效的赋值给todos */
    },
    removeAllTodo: function(todo) {
      if ( this.todos.length !== 0 && confirm('确定要删除所有已完成记事么？')) {
        this.todos = []
      }
    },
    showDoneList: function () {
      console.log(this.isShowDone);
      this.isShowDone = !this.isShowDone;
    }
  }
}
</script>

<style scoped>
.todo {
  width: 90%;
  height: 100%;
  margin: 0 auto;
  color: #fff;
  padding-bottom: 90px;
}
.add-box {
  padding: 20px 0;
}
.add-todo {
  display: block;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-radius: 20px;
  padding-left: 20px;
}
.main h3 {
  color: #fff;
  line-height: 2em;
  padding-left: 1em;
  font-size: 0.9em;
  font-weight: normal;
  margin-bottom: 0.5em;
}
.main li {
  background-color: #fff;
  color: #4fc08d;
  line-height: 2em;
  padding-left: 1em;
  margin-bottom: 0.28em;
  transition: all ease-in-out .2s;
}
.main li input[type='checkbox'] {
  vertical-align: middle;
  margin-right: 4px;
}
.active-todos li:hover {
  background-color: #eee;
}
.completed-todos {
  margin-top: 20px;
}
.completed-todos li {
  position: relative;
  background-color: #ccc;
  color: #999;
  text-decoration: line-through;
}
.list-empty {
  text-align: center;
  font-size: 0.9em;
}
.completed-todos li .remove-todo {
  position: absolute;
  right: 0;
  top: 0;
  height: 2em;
  line-height: 2em;
  padding: 0 10px;
  display: none;
  cursor: pointer;
}
.completed-todos li:hover .remove-todo {
  display: block;
}
/* action */
.footer {
  position: fixed;
  width: 100%;
  padding: 1em 0;
  bottom: 0;
  left: 0;
  background-color: #4fc08d;
  opacity: 0.85;
}
.action {
  text-align: center;
  padding-bottom: 1em;
}
.action span {
  cursor: pointer;
  margin: 0 5px;
  font-size: .9em;
}
</style>



