// localstorage methods

let storage_key = 'vuejs-todo'
let storage_isDoneShow = 'isDoneShow'
let store_save = function (key, data) {
  localStorage.setItem(key, JSON.stringify(data))
}
let store_fetch = function (key) {
  return JSON.parse(localStorage.getItem(key) || '[]')
}

export let storage_todo = {
  save: function (todos) {
    localStorage.setItem(storage_key, JSON.stringify(todos))
  },
  fetch: function () {
    return JSON.parse(localStorage.getItem(storage_key) || '[]')
  }
}

/* @isChange boolean */
export let isDoneShow = {
  toggle: function (isShowDone) {
    store_save(storage_isDoneShow, !isShowDone)
  },
  fetch: function () {
    return store_fetch(isShowDone)
  }
}

export let timestamp = function () {
  return new Date().getTime()
}

export let filters = {
  active: function (todos) {
    return todos.filter(function (todo) {
      return !todo.done
    })
  },
  done: function (todos) {
    return todos.filter(function (todo) {
      return todo.done
    })
  }
}



